import { useEffect, useState } from 'react'
import Graph from 'graphology'
import {
  useLoadGraph,
  useSetSettings,
  useSigma,
  useRegisterEvents
} from '@react-sigma/core'
import { useLayoutCirclepack } from '@react-sigma/layout-circlepack'
import { dataToSerialNodes } from '../../utils'

export const CirclePackGraph = ({dataset,nodeScale,edgeScale}) => {
 
  const sigma = useSigma()
  const loadGraph = useLoadGraph()
  const setSettings = useSetSettings()
  const registerEvents = useRegisterEvents()
  const [hoveredNode, setHoveredNode] = useState(null)
  const { assign } = useLayoutCirclepack({
    hierarchyAttributes: ['cluster']
  })

  useEffect(() => {
    setSettings({
      nodeReducer: (node, data) => {
        const graph = sigma.getGraph()
        const newData = {
          ...data,
          highlighted: data.highlighted || false
        }

        if (hoveredNode) {
          if (
            node === hoveredNode ||
            graph.neighbors(hoveredNode).includes(node)
          ) {
            newData.highlighted = true
          } else {
            newData.color = '#E2E2E2'
            newData.highlighted = false
          }
        }
        return newData
      },
      edgeReducer: (edge, data) => {
        const graph = sigma.getGraph()
        console.log(sigma,graph)
        const newData = { ...data, hidden: false }

        if (hoveredNode && !graph.extremities(edge).includes(hoveredNode)) {
          newData.hidden = true
        }
        return newData
      }
    })
  }, [hoveredNode, setSettings, sigma])

  useEffect(() => {
      const graph = new Graph()
      graph.import(dataToSerialNodes(dataset,nodeScale,edgeScale))
      loadGraph(graph)
      assign()
      registerEvents({
        enterNode: (event) => setHoveredNode(event.node),
        leaveNode: () => setHoveredNode(null)
      })
  }, [assign, dataset, loadGraph, registerEvents,edgeScale,nodeScale])
  
 

  return <></>
}
