import React, { useEffect, useState } from 'react'
import { useSigma, useSetSettings } from '@react-sigma/core'
// import { useCamera } from '../../hooks/useCamera'
import { Slider } from 'antd'

/**
 * ```jsx
 * <SigmaContainer>
 *   <ControlsContainer>
 *     <EdgeControl />
 *   </ControlsContainer>
 * </SigmaContainer>
 * ```
 * @category Component
 */

export const EdgeControl = ({ id, className, style, max, min, edgeScale }) => {
  // Get sigma
  const sigma = useSigma()
  const setSettings = useSetSettings()

  // egdeSize range
  const [minSize, setMinSize] = useState(min)
  const [maxSize, setMaxSize] = useState(max)

  useEffect(() => {
    setSettings({
      edgeReducer: (edge, data) => {
        const newData = { ...data, hidden: false }
        if (data.size > edgeScale(maxSize) || data.size < edgeScale(minSize)) {
          newData.hidden = true
        }
        sigma.setSetting(edge,newData)
        return newData
      }
    })
  }, [setSettings, sigma, edgeScale, maxSize, minSize])

  const onAfterChange = (value) => {
    setMinSize(value[0])
    setMaxSize(value[1])
  }

  // Common html props for the div
  const htmlProps = {
    className: `${className ? className : ''}`,
    id,
    style
  }

  return (
    <div {...htmlProps}>
      <h4></h4>
      <Slider
        range
        defaultValue={[min, max]}
        min={min}
        max={max}
        onAfterChange={onAfterChange}
      />
    </div>
  )
}
