const colorArr = ['#FF6B3B', '#FFC100', '#9FB40F', '#76523B', '#0E8E89',
    '#F383A2', '#247FEA', '#2BCB95', '#B1ABF4', '#1D42C2',
    '#D64BC0', '#2498D1', '#BBBDE6', '#4045B2', '#21A97A',
    '#FF745A', '#007E99', '#FFC328', '#B40F0F', '#4435FF',
    '#FF5CA2', '#BBE800', '#004988'
]

export const dataToSerialNodes = (jsonData,nodeScale,edgeScale) => {
    const nodes = jsonData.nodes.map(d => {
        return {
            key: d.id,
            attributes: {
                key: d.id,
                label: d.name,
                id: d.id,
                cluster: d.category.toString(),
                x: d.x,
                y: d.y,
                size: nodeScale(d.symbolSize),
                color: colorArr[d.category]
            }
        }
    })
    //.filter(d=>d.size>=100)
    const edges = jsonData.edges.map((d, index) => {
        return {
            key: "edges" + index,
            source: d.source,
            target: d.target,
            attributes: {
                size: edgeScale(d.size),
            },
        }
    })
    return {
        attributes: {
            name: "My Graph"
        },
        nodes,
        edges,
        "options": { "type": "undirected", "multi": false, "allowSelfLoops": true }
    }

}